import React,{Component} from 'react';
import {CardList} from './components/card-list/card-list.components'
import './App.css';
import { Searchbox } from './components/search-box/search-box.components';

class App extends Component
{
  constructor()
  {
    super();
    this.state=
    {
      monsters:[],
      searchField:""
    };
  }

  componentDidMount()
  {
    fetch('https://jsonplaceholder.typicode.com/users')
    .then(response=>response.json())
    .then(value => this.setState({monsters:value}));
  }

  handlechange=(e)=>
  {
    this.setState({searchField:e.target.value})
  }

  handlechange1()
  {
    console.log("Hello");
  }

  render()
  {
    //const monsters=this.state.monsters;
   const filteredMons=this.state.monsters.filter(monster=>monster.name.toLowerCase().includes(this.state.searchField.toLowerCase()));
  
   return (
    <div className="App">
      <h1 class="title">Monsters Rolodex</h1>
      <Searchbox placeholder="Search Monsters" changeHandler={this.handlechange}></Searchbox>
      <CardList name={filteredMons}>
      
      </CardList>
    </div>
   );
  }
}

export default App;
